
.page-me .relative {
  position: relative;
}
.page-me .bg {
  background-image: url(../assets/bg.png);
  height: 260px;
}
.page-me .wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 15px;
}
.page-me .wrapper .center {
    color: #fff;
    font-size: 18px;
    margin-bottom: 10px;
}
.page-me .wrapper .center img {
      display: inherit;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      border: 2px solid rgba(236, 236, 236, 0.25);
}
.page-me .wrapper .name, .page-me .wrapper .name a {
    color: #fff;
}
.page-me .wrapper .grid-wrapper {
    width: 100%;
}
.page-me .wrapper .grid-wrapper .weui-grids:before, .page-me .wrapper .grid-wrapper .weui-grids:after {
      display: none;
}
.page-me .wrapper .grid-wrapper .weui-grids .grid-item:before, .page-me .wrapper .grid-wrapper .weui-grids .grid-item:after {
      display: none;
}
.page-me .wrapper .grid-wrapper .weui-grids .grid-item:nth-child(1):before {
      display: block;
      top: 30px;
      bottom: 30px;
      border-color: #fff;
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
}
.page-me .wrapper .grid-wrapper .weui-grids .grid-item:hover {
      background: none;
}
.page-me .grid-item {
  padding: 15px 12px;
  text-align: center;
  font-size: 12px;
  color: #333;
}
.page-me .grid-item .title {
    display: block;
    margin-bottom: 5px;
    color: #fff;
    font-size: 1.5em;
}
.page-me .grid-item span {
    font-size: 1.3em;
    color: #ff9800;
}
.page-me .panel, .page-me .integral {
  background: #fff;
}
.page-me .panel.panel, .page-me .integral.panel {
    position: absolute;
    left: 15px;
    right: 15px;
    bottom: -50px;
    border-radius: 5px;
    box-shadow: 5px 5px 15px rgba(157, 9, 6, 0.35);
}
.page-me .panel.panel .weui-grids:before, .page-me .panel.panel .weui-grids:after, .page-me .integral.panel .weui-grids:before, .page-me .integral.panel .weui-grids:after {
      display: none;
}
.page-me .panel.panel .weui-grids .grid-item, .page-me .integral.panel .weui-grids .grid-item {
      padding: 15px 12px;
}
.page-me .panel.panel .weui-grids .grid-item .label, .page-me .integral.panel .weui-grids .grid-item .label {
        display: block;
}
.page-me .panel.panel .weui-grids .grid-item .vux-x-icon, .page-me .integral.panel .weui-grids .grid-item .vux-x-icon {
        fill: #666;
}
.page-me .panel.panel .weui-grids .grid-item span, .page-me .integral.panel .weui-grids .grid-item span {
        font-size: 1.2em;
        color: #999;
}
.page-me .panel.panel .weui-grids .grid-item:after, .page-me .integral.panel .weui-grids .grid-item:after {
        display: none;
}
.page-me .panel.panel .weui-grids .grid-item:nth-child(3):before, .page-me .integral.panel .weui-grids .grid-item:nth-child(3):before {
        display: none;
}
.page-me .panel.integral, .page-me .integral.integral {
    margin-top: 90px;
}
.page-me .panel.integral .grid-item, .page-me .integral.integral .grid-item {
      padding: 20px 12px;
}
.page-me .panel.integral .grid-item .label, .page-me .integral.integral .grid-item .label {
        display: block;
}
.page-me .panel.integral .grid-item .vux-x-icon, .page-me .integral.integral .grid-item .vux-x-icon {
        fill: #666;
}
.page-me .panel.integral .grid-item span, .page-me .integral.integral .grid-item span {
        color: #999;
}



