
.page-profile .header .weui-cells {
  margin: 0;
}
.page-profile .header .weui-cells .weui-cell {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}
.page-profile .header .weui-cells img {
    width: 64px;
