
.page-address .card {
  margin-top: 15px;
  box-shadow: 2px 2px 1px rgba(204, 204, 204, 0.51);
}
.page-address .card:before, .page-address .card:after {
    display: none;
}
.page-address .card .header {
    padding: 10px 0;
}
.page-address .card .header .row {
      padding: 5px 15px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      color: #666;
}
.page-address .card .footer {
    padding: 15px;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #666;
}
.page-address .card .footer:before {
      content: " ";
      position: absolute;
      left: 0;
      right: 0;
      height: 1px;
      top: 0;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
.page-address .card .footer .align-right {
      text-align: right;
      font-size: .8em;
}
.page-address .card .footer .weui-btn {
      margin: 0;
}
.page-address .card .footer .weui-btn .vux-x-icon {
        vertical-align: middle;
        margin-top: -4px;
        fill: #fff;
}
.page-address .card .footer .weui-cells_checkbox {
      margin: 0;
}
.page-address .card .footer .weui-cells_checkbox:before, .page-address .card .footer .weui-cells_checkbox:after {
        display: none;
}
.page-address .card .footer .weui-cells_checkbox label {
        padding: 0;
}
.page-address .card .footer .weui-cells_checkbox label:before, .page-address .card .footer .weui-cells_checkbox label:after {
          display: none;
}
.page-address .card .content {
    position: relative;
    padding: 20px 15px;
    color: #666;
}
.page-address .card .content:before {
      content: " ";
      position: absolute;
      left: 0;
      right: 0;
      height: 1px;
      top: 0;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
.page-address footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
.page-address footer button {
    border-radius: 0;
}



