
.page-ingegal-exchange-record .head {
  padding: 5px 15px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.page-ingegal-exchange-record .list .weui-cells {
  margin: 0;
}
.page-ingegal-exchange-record .list .weui-cell {
  color: #333;
}
.page-ingegal-exchange-record .list .weui-cell:before {
    bottom: 0 !important;
    top: auto;
    left: 0px;
}
.page-ingegal-exchange-record .list .weui-cell:last-child:before {
    display: none;
}
.page-ingegal-exchange-record .list .weui-cell_access .weui-cell__ft:after {
  height: 12px;
  width: 12px;
  border-color: #333;
  margin-top: -8px;
}



