
.page-sigup .form-group .weui-cells {
  border-radius: 4px;
  box-shadow: 1px 1px 2px #ccc;
}
.page-sigup .form-group .weui-cells:before, .page-sigup .form-group .weui-cells:after {
    display: none;
}
.page-sigup .form-group .weui-cells .vux-x-input {
    padding: 12px 15px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.page-sigup .form-group .weui-cells .vux-x-input .weui-cell__hd {
      width: 30%;
}
.page-sigup .wp {
  padding: 15px 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.page-sigup .wp, .page-sigup .wp a {
    color: #999;
}
.page-sigup .wp .link {
    color: #E64340;
}
.page-sigup .countdown {
  display: inline-block;
  position: relative;
  padding-left: 15px;
  font-size: 14px;
}
.page-sigup .countdown:before {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    left: 0;
    vertical-align: middle;
    background: #adadad;
}



