
.page-message .card {
  border-radius: 6px;
  box-shadow: 2px 2px 1px rgba(204, 204, 204, 0.51);
}
.page-message .card:before, .page-message .card:after {
    display: none;
}
.page-message .card .header {
    padding: 15px;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #666;
}
.page-message .card .header:before {
      content: " ";
      position: absolute;
      left: 0;
      right: 0;
      height: 1px;
      bottom: 0;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
.page-message .card .content {
    padding: 20px 15px;
    color: #666;
}



